.si-unit-editor{
  display: flex;
  justify-content: center;
  padding: .5rem 1rem;
  border-bottom: 1px solid #eee;
}

.si-unit-container{
  display: flex;
  /* margin-left: 1rem; */
  transform: scale(.8);
}
.si-unit-container input{
  color: white !important;
}
.si-unit-editor .si-unit-exponent{
  position: relative;
  top: -1rem;
  margin-left: 1rem;
}

.si-unit-separator{
  margin-left: 1rem;
}

.si-unit-mathjax{
  display: flex;
  justify-content: center;
  align-items: center;
  transform: scale(1.2);
}


.si-unit-editor-add-unit{
  padding: .5rem 1.2rem;
  display: flex;
  justify-content: flex-start;
  justify-content: space-between;
  align-items: center;
  border-bottom: 1px solid #eee;
  /* border-top: 2px solid #eee; */
  color: var(--orange-600);
  color: white;
  color: var(--orange-600);
  background-color: #f6cba8;
  background-color: #eee;

  /* box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19); */
}


.si-unit-editor-add-unit .si-unit-editor-add-button{

  font-size: 2rem;
  background-color: rgba(0, 0, 0, 0.1);
  display: flex;
  justify-content: center;
  align-items: center;
  padding: .5rem;
  cursor: pointer;
  border-radius: 4px;
  box-shadow: 0 1px 2px 0 rgba(0, 0, 0, 0.2);
}
.si-unit-editor-add-unit .si-unit-editor-add-button:hover{
  background-color: rgba(0, 0, 0, 0.1);
}

.si-unit-text{
  padding: .5rem 0.3rem;
}


@media (min-width: 768px){
  .si-unit-editor .si-unit-exponent,
  .up-down-input-component.up-down-input-component--container{
    min-width: 5rem;
  }
  .si-unit-editor-add-unit .si-unit-editor-add-button{
    padding: .8rem;
    font-size: 2.5rem;
  }
  .si-unit-editor-add-unit > p{
    font-size: 1.5rem;
  }
}